import Swiper from 'swiper';
import 'ScssComponents/advantages.scss';
import { exist } from '../utils';
import 'swiper/css';

const advantages = (
	sectionSelector = '.js-advantages-section',
	sliderSelector = '.js-advantages-slider',
) => {
	const $sections = document.querySelectorAll(sectionSelector);
	if (!exist($sections)) return;

	$sections.forEach($section => {
		const $slider = $section.querySelector(sliderSelector);
		if (!exist([$slider])) return;

		const advantagesSwiper = new Swiper($slider, {
			speed: 800,
			breakpoints: {
				320: {
					slidesPerView: 2.2,
				},
				768: {
					slidesPerView: 3.3,
				},
				1023: {
					slidesPerView: 4,
				},
			},
		});
	});
};

export default advantages;
