import Swiper, { Navigation } from 'swiper';
import { exist } from '../utils';
import 'swiper/css';

const resourcesSlider = (
	sectionSelector = '.js-resources-section',
	sliderSelector = '.js-resources-slider',
	btnNextSelector = '.js-resources-next-button',
	btnPrevSelector = '.js-resources-prev-button',
) => {
	const $sections = document.querySelectorAll(sectionSelector);
	if (!exist($sections)) return;

	Swiper.use([Navigation]);

	$sections.forEach($section => {
		const $slider = $section.querySelector(sliderSelector);
		const $btnNext = $section.querySelector(btnNextSelector);
		const $btnPrev = $section.querySelector(btnPrevSelector);
		if (!exist([$slider, $btnNext, $btnPrev])) return;

		const resourcesSwiper = new Swiper($slider, {
			speed: 800,
			slidesPerView: 'auto',
			navigation: {
				nextEl: $btnNext,
				prevEl: $btnPrev,
			},
		});
	});
};

export default resourcesSlider;
