import { exist } from '../utils';

const heroVideo = (
	sectionSelector = '.js-hero-section',
	video = '.js-hero-video',
) => {
	const $sections = document.querySelector(sectionSelector);
	if (!exist($sections)) return;

	const $video = $sections.querySelector(video);
	// const player = new Plyr($video);
};

export default heroVideo;
