import Swiper, { Navigation, Pagination, EffectFade } from 'swiper';
import { exist } from '../utils';
import 'swiper/css';
import 'swiper/css/effect-fade';
import 'ScssComponents/banner-slider.scss';
import 'ScssComponents/universal/slider-controls.scss';

const bannerSlider = (
	sectionSelector = '.js-banner-section',
	sliderSelector = '.js-banner-slider',
	btnNextSelector = '.js-banner-next-button',
	btnPrevSelector = '.js-banner-prev-button',
	paginationSelector = '.js-banner-pagination',
) => {
	const $sections = document.querySelectorAll(sectionSelector);
	if (!exist($sections)) return;

	Swiper.use([Navigation, Pagination, EffectFade]);

	$sections.forEach($section => {
		const $slider = $section.querySelector(sliderSelector);
		const $btnNext = $section.querySelector(btnNextSelector);
		const $btnPrev = $section.querySelector(btnPrevSelector);
		const $pagination = $section.querySelector(paginationSelector);
		if (!exist([$slider, $btnNext, $btnPrev, $pagination])) return;

		const bannerSwiper = new Swiper($slider, {
			speed: 800,
			slidesPerView: 1,
			autoHeight: true,
			effect: 'fade',
			fadeEffect: {
				crossFade: true,
			},
			navigation: {
				nextEl: $btnNext,
				prevEl: $btnPrev,
			},
			pagination: {
				el: $pagination,
				type: 'fraction',
			},
		});
	});
};

export default bannerSlider;
